___CSS_LOADER_EXPORT___.push([module.id, `.em-tooltip {
    font-family: Arial;
    color: #383838;
    max-width: 300px;
    overflow: hidden;
    background: white;
    padding: 0px;
    border: 0px;
    border-radius: 0px;
    box-shadow:
        0px 4px 6px rgba(0, 0, 0, 0.1),
        0px 1px 3px rgba(0, 0, 0, 0.08);
    position: absolute;
    font-family: Helvetica, Arial, sans-serif;
    pointer-events: none;
    z-index: 99999999;
}
.em-tooltip-text {
    background: #ffffff;
    padding: 4px;
    font-size: 15px;
}
.em-tooltip-bar {
    background: #515560;
    color: #ffffff;
    padding: 6px;
    font-size: 16px;
}

.em-footnote-tooltip {
    background: #ffffff;
    padding: 4px;
    font-size: 12px;
}
.em-tooltip-table {
    margin: 0px;
}
.em-tooltip-piechart-container {
    text-align: center;
    fill: #383838;
    display: flex;
    justify-content: center;
}
.em-tooltip-breakdown {
    display: flex;
    flex-direction: column;
    gap: 2px;
    font-size: 13px;
    line-height: 1.2;
    padding: 10px;
    padding-top: 3px;
}

.em-breakdown-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.em-breakdown-color {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    display: inline-block;
    margin-right: 6px;
    flex-shrink: 0;
}

.em-breakdown-label {
    flex: 1;
    color: #333;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.em-breakdown-value {
    margin-left: 8px;
    font-weight: 500;
    color: #222;
    white-space: nowrap;
}

.em-breakdown-item.em-total {
    border-top: 1px solid #ccc;
    margin-top: 3px;
    padding-top: 3px;
    font-weight: bold;
}

.em-cx-breakdown-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.em-tooltip-cx {
    line-height: 1;
    padding: 0px;
}

.em-tooltip-cx .em-breakdown-value,
.em-tooltip-cx .em-breakdown-label,
.em-tooltip-cx .em-breakdown-color {
    margin: 0;
    font-size: 11px; /* Smaller text for compactness */
    white-space: nowrap;
}

.em-tooltip-cx-table {
    width: 100%;
    border-collapse: collapse;
    table-layout: fixed;
}

.em-tooltip-cx-table th,
.em-tooltip-cx-table td {
    padding: 2px 4px; /* Minimal padding */
    border: 1px solid #eee;
}

.em-tooltip-cx-table th {
    background: #f7f7f7;
    font-weight: bold;
    font-size: 11px;
}

.em-tooltip-cx-table td {
    font-size: 11px;
    white-space: nowrap;
}

/* Make the Total value bold and right-aligned */
.em-tooltip-cx-table .em-total td[colspan] {
    text-align: right;
    font-weight: bold !important;
}

/* Also make the inner number span bold */
.em-tooltip-cx-table .em-total td[colspan] .em-breakdown-value {
    font-weight: bold !important;
}
.em-total {
    font-weight: bold !important;
}
