.em-map-wrapper {
    position: relative;
    display: inline-block;
}

.em-map-wrapper > svg {
    display: block;
}

/* General text settings */
text {
    pointer-events: none;
    font-family: Arial, Helvetica, sans-serif;
}

a text {
    pointer-events: all;
}

/* Main map */
.em-map text,
.em-legend text,
.em-title,
.em-subtitle,
.em-footnote {
    font-family: Arial, Helvetica, sans-serif;
}

.em-map.em-draggable g {
    cursor: grab;
}

.em-map.em-dragging g {
    cursor: grabbing;
    cursor: -webkit-grabbing;
}

/* Coastal margin */
.em-coastal-margin {
    filter: url(#em-coastal-blur);
    stroke-linejoin: round;
    stroke-linecap: round;
    fill: none;
    stroke-width: 1;
    stroke: rgb(0, 52, 107);
}

/* Title and subtitle styling */
.em-title,
.em-inset-title {
    fill: #4c4b4b;
    stroke: none;
}

.em-title {
    font-size: 21px;
    font-weight: bold;
}

.em-subtitle,
.em-inset-subtitle {
    fill: #4c4b4b;
    fill-opacity: 0.9;
    stroke: none;
    stroke-linejoin: round;
    paint-order: stroke;
}

.em-subtitle {
    font-size: 18px;
    font-weight: 100;
}

.em-source-pretext {
    fill: #848280;
    font-size: 11px;
    transform: translate(-5px, -5px);
}

.em-source-dataset-link {
    font-size: 11px;
    fill: #0e47cb;
    cursor: pointer !important;
    text-decoration: underline;
    transform: translate(-5px, -5px);
}

.em-source-dataset-link:hover {
    fill: #082b7a;
    cursor: pointer !important;
    text-decoration: underline;
}

.em-frame {
    stroke-width: 0;
    stroke: grey;
}

/* Graticule styling */
.em-graticule {
    stroke: lightgray;
    stroke-width: 1;
}
.em-frame,
.em-graticule {
    fill: none;
    pointer-events: none;
}

.em-sea {
    fill: white;
    /* pointer-events: none; */
}

.em-scalebar-line {
    stroke: #696969;
    stroke-width: 0.8px;
}

.em-scalebar-label {
    font-size: 9px;
    text-anchor: middle;
}

/* Bottom text and symbol styling */
.em-footnote {
    fill: #848280;
    pointer-events: all;
    cursor: pointer !important;
    font-size: 11px;
    transform: translate(5px, -5px);
}

.em-footnote a {
    fill: #0e47cb;
    text-decoration: underline;
}

.em-footnote a:hover {
    fill: #0a328e;
}

.em-centroid,
.em-coxcomb g {
    cursor: pointer !important;
}

.em-flow-link {
    cursor: pointer !important;
}

.em-flow-link-outline {
    cursor: pointer !important;
}

.em-flow-labels {
    font-size: 15px;
    stroke-width: 1px;
    fill: #696969;
    pointer-events: none;
}

.em-flow-labels-halo {
    font-size: 15px;
    stroke-width: 2px;
    fill: white;
    pointer-events: none;
}

.em-grid-rect,
.em-grid-hexagon {
    stroke: white;
}

.em-flow-donuts {
    cursor: pointer !important;
}
